@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
.container-loader {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.container-loader div {
  -webkit-animation: 1.5s bounce infinite ease-in-out both;
          animation: 1.5s bounce infinite ease-in-out both;
  background-color: var(--blue-links);
  border-radius: 50%;
  display: inline-block;
  height: 3rem;
  width: 3rem;
}

.container-loader .bounce1 {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

.container-loader .bounce2 {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes bounce {
  0%,
  80%,
  100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}

@keyframes bounce {
  0%,
  80%,
  100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}

.auth-container {
