.cursor {
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  background-color: white;
  display: inline-block;
  margin-left: 0.1rem;
  width: 3px;
}

.typed {
  -webkit-animation: none;
          animation: none;
