#burguer-menu {
  cursor: pointer;
  float: right;
  height: 27px;
  margin-right: 30px;
  margin-top: 30px;
  position: relative;
  width: 27px;
  z-index: 2001;
}

#burguer-menu span,
#burguer-menu span::before,
#burguer-menu span:after {
  background: var(--gray-text);
  display: block;
  height: 4px;
  position: absolute;
  transition: all 300ms ease;
}

#burguer-menu span::before,
#burguer-menu span:after {
  content: "";
}

#burguer-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}

#burguer-menu span::before {
  right: 0px;
  top: -10px;
  width: 16px;
}

#burguer-menu span::after {
  right: 0px;
  top: 10px;
  width: 20px;
}

#burguer-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}

#burguer-menu.close span::before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#burguer-menu.close span::after {
  left: 0px;
  opacity: 0;
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#floating-button {
  background: var(--dark-background);
  border-radius: 50%;
  bottom: 0;
  box-shadow: inset 2px 2px 5px var(--dark-border),
    inset -3px -3px 7px var(--ligth-border);
  color: var(--blue-links);
  cursor: pointer;
  padding: 10px;
  position: fixed;
  right: 0;
  margin-right: 20px;
  margin-bottom: 15px;
  z-index: 2000;
}

#menu {
