  display: flex;
  justify-content: center;
  left: 0px;
  min-height: 100%;
  min-width: 100%;
  opacity: 0;
  position: fixed;
  text-align: center;
  top: 0px;
  transition: all 300ms ease;
  visibility: hidden;
  z-index: 2000;
}

#menu.show {
  background: var(--dark-background);
  opacity: 1;
  visibility: visible;
}

#menu ul {
