@media (max-width: 750px) {
  #burguer-menu {
    margin-right: 15px;
    margin-top: 15px;
  }

  #floating-button {
    margin-right: 15px;
    margin-top: 15px;
  }
}

:root {
  --blue-links: #3498db;
  --color-codepen: #47cf73;
  --color-github: #c9510c;
  --dark-background: #131419;
  --dark-border: rgba(0, 0, 0, 0.5);
  --error-text: #ff0047;
  --gray-text: #8c8c8c;
  --ligth-border: rgba(255, 255, 255, 0.05);
}

body {
  background: #131419;
  background: var(--dark-background);
  box-sizing: border-box;
  color: #8c8c8c;
  color: var(--gray-text);
  font-family: "Poppins", sans-serif;
  margin: 0;
  overflow-x: hidden;
  scroll-behavior: smooth;
  padding: 0;
}

/* Estilos para componentes globales */

a {
  color: #3498db;
  color: var(--blue-links);
  font-size: 16px;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1,
h2,
h3 {
  color: white;
}

p {
  font-size: 16px;
}

blockquote {
  font-style: italic;
}

button {
  background: #131419;
  background: var(--dark-background);
  border-radius: 25px;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5), -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: 2px 2px 5px var(--dark-border), -3px -3px 7px var(--ligth-border);
  color: #8c8c8c;
  color: var(--gray-text);
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  outline: none;
}

button:focus {
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.5),
    inset -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: inset 2px 2px 5px var(--dark-border),
    inset -3px -3px 7px var(--ligth-border);
  color: #3498db;
  color: var(--blue-links);
}

input,
select,
textarea {
  background: #131419;
  background: var(--dark-background);
  border: none;
  border-radius: 10px;
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.5),
    inset -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: inset 2px 2px 5px var(--dark-border),
    inset -3px -3px 7px var(--ligth-border);
  caret-color: #3498db;
  caret-color: var(--blue-links);
  color: #8c8c8c;
  color: var(--gray-text);
  font-size: 18px;
  outline: none;
  padding-left: 45px;
  resize: none;
  width: 100%;
}

input:focus,
select:focus,
textarea:focus {
  box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.5),
    inset -1px -1px 2px rgba(255, 255, 255, 0.05);
  box-shadow: inset 1px 1px 1px var(--dark-border),
    inset -1px -1px 2px var(--ligth-border);
}

input::-webkit-file-upload-button {
  display: none;
  padding-top: 20px;
}

/* Estilos para clases globales */

.neumorphism {
  background: #131419;
  background: var(--dark-background);
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5), -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: 2px 2px 5px var(--dark-border), -3px -3px 7px var(--ligth-border);
  z-index: 1000;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 20px;
  padding-bottom: 50px;
}

.field .input-file {
  cursor: pointer;
  padding-top: 15px;
}

.input-file {
  height: 35px !important;
}

.button {
  background: #131419;
  background: var(--dark-background);
  border: none;
  border-radius: 25px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5), -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: 2px 2px 5px var(--dark-border), -3px -3px 7px var(--ligth-border);
  color: #3498db;
  color: var(--blue-links);
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  height: 50px;
  outline: none;
  padding: 10px;
  text-decoration: none;
}

.button:hover {
  text-decoration: none;
}

.button:focus {
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.5),
    inset -3px -3px 7px rgba(255, 255, 255, 0.05);
  box-shadow: inset 2px 2px 5px var(--dark-border),
    inset -3px -3px 7px var(--ligth-border);
}

.main-info {
  letter-spacing: 1px;
  padding-top: 40px;
  text-transform: uppercase;
  text-align: center;
}

.main-info h2 {
  color: #3498db;
  color: var(--blue-links);
}

.main-info h1 {
  margin-top: -20px;
}
/* Estilos para animar el fondo */

.box div {
  background-color: transparent;
  border: 6px solid rgba(255, 255, 255, 0.05);
  border: 6px solid var(--ligth-border);
  height: 60px;
  position: absolute;
  width: 60px;
}

.box div:nth-child(1) {
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
  left: 42%;
  top: 12%;
}

.box div:nth-child(2) {
  -webkit-animation: animate 7s linear infinite;
          animation: animate 7s linear infinite;
  left: 50%;
  top: 70%;
}

.box div:nth-child(3) {
  -webkit-animation: animate 9s linear infinite;
          animation: animate 9s linear infinite;
  left: 6%;
  top: 17%;
}

.box div:nth-child(4) {
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
  left: 60%;
  top: 20%;
}

.box div:nth-child(5) {
  -webkit-animation: animate 6s linear infinite;
          animation: animate 6s linear infinite;
  left: 10%;
  top: 67%;
}

.box div:nth-child(6) {
  -webkit-animation: animate 12s linear infinite;
          animation: animate 12s linear infinite;
  left: 70%;
  top: 80%;
}

.box div:nth-child(7) {
  -webkit-animation: animate 15s linear infinite;
          animation: animate 15s linear infinite;
  left: 80%;
  top: 60%;
}

.box div:nth-child(8) {
  -webkit-animation: animate 16s linear infinite;
          animation: animate 16s linear infinite;
  left: 25%;
  top: 32%;
}

.box div:nth-child(9) {
  -webkit-animation: animate 9s linear infinite;
          animation: animate 9s linear infinite;
  left: 25%;
  top: 90%;
}

.box div:nth-child(10) {
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
  left: 80%;
  top: 20%;
}

@-webkit-keyframes animate {
  0% {
    opacity: 1;
    transform: scale(0) translateY(0) rotate(0);
  }
  100% {
    opacity: 0;
    transform: scale(1.3) translateY(-90px) rotate(360deg);
  }
}

@keyframes animate {
  0% {
    opacity: 1;
    transform: scale(0) translateY(0) rotate(0);
  }
  100% {
    opacity: 0;
    transform: scale(1.3) translateY(-90px) rotate(360deg);
  }
}

